import "./style.css";
import PropertyVault from './PropertyVault';
import myProperty from './myProperty';
import Header from './Header';

import React, { Component } from 'react'
import PropertyValuts from "./PropertyValuts";
import SeletedProperty from "./SeletedProperty";
import { FaLock } from "react-icons/fa"
class index extends Component {
    constructor(props) {
        super(props)

        this.state = {
            selectedProperty:true
        }
    }
    property=(locked,saving,sellLess)=>{
        return(
            <div className="property">
            <div className="propety-left-item-wapper">
                {locked ? <FaLock color={"red"} size={"20px"} /> : <div style={{"width":"20px"}}></div>}
                <div className="image-container">image</div>
            </div>
            <div className="propety-right-item-wapper">
                <div className={`${saving && "currently-saving"} property-info`}>
                    {saving && <h3 className="currently-saving-txt">Currently saving</h3>}
                    <div>
                        {!saving && <h3 className="property-price">2,000,000</h3> }
                        <div className="small-text">VGC,Ajah</div> 
                        {!saving && <div className="small-text">With 3 other</div>} 
                    </div>
                </div>
                <div className={`
                            ${ (saving && sellLess) ? "not-saving" : ""} propety-right-right 
                            ${!sellLess && !saving && "percent-condition"}`}>
                    {!saving && <span>+2.34</span>}
                    {
                        !saving ? <div className="small-text">{(sellLess &&  "sell lease")}</div>:
                        <div className="saving-h4-wapper">
                            <h5>40%</h5>
                            <div>saving</div>
                        </div>
                    }
                </div>
            </div>
        </div>
        );
    }
    render() {
        return (
            <div className="property-container">
                <div className="property-left-wrapper cover">
                    {this.state.selectedProperty ? <SeletedProperty /> : <PropertyValuts /> }
                </div>
                <div className="property-right-wrapper cover">
                    <h3>My Properties: 6</h3>
                    <div className="property-selection">
                        <button onClick={()=>{this.setState({propertyTabSWith:"all"})}} className={`${this.state.propertyTabSWith == "all" ? "selected-property-tab" : "unselected-property-tab"}`}>all</button>
                        <button onClick={()=>{this.setState({propertyTabSWith:"acquired"})}} className={`${this.state.propertyTabSWith == "acquired" ? "selected-property-tab" : "unselected-property-tab"}`}>acquired</button>
                        <button onClick={()=>{this.setState({propertyTabSWith:"savings"})}} className={`${this.state.propertyTabSWith == "savings" ? "selected-property-tab" : "unselected-property-tab"}`}>savings</button>
                    </div>
                    <div className="property-wrapper">
                        {this.property(true,false,true)}
                        {this.property(true,true,true)}
                        {this.property(false,false,false)}    
                        {this.property(true,false,true)}
                        {this.property(true,true,true)}
                        {this.property(true,false,false)}
                    </div>    
                </div>
            </div>
            )
        }
    }

    export default index
    // {this.property(false,true,false)}
    // {this.property(false,true,false)}
    // {this.property(false,true,false)}
    